import React, { useState } from "react";
import {
  Form,
  Container,
  Button,
  Row,
  Col,
  Image,
  Alert,
} from "react-bootstrap";
import ContactUs from "../assets/contact-us.png";
import { db } from "../config/firebase";

const Contact = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [subject, setSubject] = useState("");
  const [message, setMessage] = useState("");
  const [success, setSuccess] = useState(false);

  const [loader, setLoader] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoader(true);
    db.collection("contacts")
      .add({
        name: name,
        email: email,
        subject: subject,
        message: message,
      })
      .then(() => {
        setSuccess(true);
        setLoader(false);
      })
      .catch((error) => {
        alert(error.message);
      });
    setName("");
    setEmail("");
    setSubject("");
    setMessage("");
    setSuccess(false);
  };

  return (
    <div>
      <Container className="p-3">
        <Row>
          <Col className="col-lg-6 col-12">
            <h3 className="fw-bold">Contact Us</h3>
            <Form onSubmit={handleSubmit}>
              <Form.Group className="mb-3 col-lg-6" controlId="formGroupText">
                <Form.Label className="fw-bold">Name:</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Full Name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3 col-lg-6" controlId="formGroupEmail">
                <Form.Label className="fw-bold">Email address:</Form.Label>
                <Form.Control
                  type="email"
                  placeholder="Enter email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3 col-lg-8" controlId="formGroupText">
                <Form.Label className="fw-bold">Subject:</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Subject"
                  value={subject}
                  onChange={(e) => setSubject(e.target.value)}
                />
              </Form.Group>
              <Form.Group className="mb-3 col-lg-12" controlId="formGroupText">
                <Form.Label className="fw-bold">Message:</Form.Label>
                <Form.Control
                  as="textarea"
                  placeholder="Leave your message here"
                  style={{ height: "75px" }}
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                />
              </Form.Group>
              <Button
                variant="primary"
                className="text-white"
                type="submit"
                disabled={loader ? true : false}
              >
                Submit
              </Button>

              {success ? (
                <div>
                  <Alert
                    className="my-3 rounded-pill fw-bold"
                    variant="success"
                  >
                    Message Submitted Successfully
                  </Alert>
                </div>
              ) : (
                ""
              )}
            </Form>
          </Col>
          <Col className="col-lg-6 col-12">
            <Image
              src={ContactUs}
              style={{ width: "75%" }}
              className=" mx-auto align-middle d-block"
              roundedCircle
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Contact;
