import React, { useState, useContext } from "react";
import { Form, Button, Container, Alert } from "react-bootstrap";
import { Link, Redirect } from "react-router-dom";
import { auth } from "../config/firebase";
import firebase from "firebase";

const SignUp = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkbox, setCheckbox] = useState(false);
  const [checkboxnotchecked, setCheckboxnotchecked] = useState(false);
  const [error, setError] = useState(false);
  const [errormessage, setErrormessage] = useState("false");
  const [success, setSuccess] = useState(false);
  const [displayName, setDisplayName] = useState("");
  const [currentUser, setCurrentUser] = useState(null);

  const createUserWithEmailAndPasswordHandler = (event, email, password) => {
    event.preventDefault();
    if (checkbox) {
      auth
        .createUserWithEmailAndPassword(email, password)
        .then((u) => {
          console.log("Successfully Signed Up");
          setCurrentUser(true);
          setSuccess(true);
        })
        .catch((err) => {
          console.log("Error: " + err.toString());
          setError(true);
          setErrormessage(err.toString().substring(7));
        });
      setEmail("");
      setPassword("");
      setCheckbox("");
      setSuccess(false);
      setCheckboxnotchecked(false);
      setDisplayName("");
    } else {
      setCheckboxnotchecked(true);
    }
  };

  const onChangeHandler = (event) => {
    const { name, value } = event.currentTarget;
    if (name === "userEmail") {
      setEmail(value);
      console.log(email);
    } else if (name === "userPassword") {
      setPassword(value);
    } else if (name === "checkBox") {
      setCheckbox(value);
      console.log(checkbox);
    } else if (name === "displayName") {
      setDisplayName(value);
    }
  };

  if (currentUser) {
    return <Redirect to="/dashboard" />;
  }

  return (
    <div>
      <Container className="p-3">
        <h2 className="fw-bold">Sign Up</h2>
        <Form className="col-lg-4">
          {success ? (
            <div>
              <Alert className="my-3" variant="success">
                Signed Up Successfully Successfully
              </Alert>
            </div>
          ) : (
            ""
          )}
          {error ? (
            <div>
              <Alert className="my-3" variant="danger">
                {errormessage}
              </Alert>
            </div>
          ) : (
            ""
          )}
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label className="fw-bold">Email address:</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter email"
              name="userEmail"
              value={email}
              id="userEmail"
              onChange={(event) => onChangeHandler(event)}
            />
            <Form.Text className="text-muted">
              We'll never share your email with anyone else.
            </Form.Text>
          </Form.Group>

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label className="fw-bold">Password:</Form.Label>
            <Form.Control
              type="password"
              placeholder="Password"
              name="userPassword"
              value={password}
              id="userPassword"
              onChange={(event) => onChangeHandler(event)}
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicCheckbox">
            <Form.Check
              defaultChecked={checkbox}
              name="checkBox"
              type="checkbox"
              label="Accept Terms and Conditions"
              onChange={(event) => onChangeHandler(event)}
            />
          </Form.Group>
          {checkboxnotchecked ? (
            <div>
              <Alert className="my-3" variant="danger">
                Agree to the Terms and Conditions
              </Alert>
            </div>
          ) : (
            ""
          )}
          <Button
            className="text-white"
            variant="primary"
            onClick={(event) => {
              createUserWithEmailAndPasswordHandler(event, email, password);
            }}
          >
            Sign Up
          </Button>
          <p className="text-center my-3">
            Already have an account? <Link to="/signIn">Sign in here</Link>{" "}
            <br /> <Link to="/passwordReset">Forgot Password?</Link>
          </p>
        </Form>
      </Container>
    </div>
  );
};
export default SignUp;
