import React, { useState, useContext } from "react";
import { Form, Button, Container, Alert, Spinner } from "react-bootstrap";
import { Link, Redirect } from "react-router-dom";
import { auth } from "../config/firebase";
import { AuthContext } from "../contexts/AuthContext";

const Signin = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(false);
  const [errormessage, setErrormessage] = useState("false");
  const [success, setSuccess] = useState(false);
  const [loader, setLoader] = useState(false);

  const signInWithEmailAndPasswordHandler = (event, email, password) => {
    event.preventDefault();
    setLoader(true);
    auth
      .signInWithEmailAndPassword(email, password)
      .then((u) => {
        console.log("Successfully Logged In");
        setSuccess(true);
        setEmail("");
        setPassword("");
        setError(false);
        setLoader(false);
        setTimeout(() => {
          return <Redirect push to="/dashboard" />;
        }, 3000);
      })
      .catch((err) => {
        console.log("Error: " + err.toString());
        setError(true);
        setErrormessage(err.toString().substring(7));
        setLoader(false);
      });
  };

  const onChangeHandler = (event) => {
    const { name, value } = event.currentTarget;

    if (name === "userEmail") {
      setEmail(value);
    } else if (name === "userPassword") {
      setPassword(value);
    }
  };

  const { currentUser } = useContext(AuthContext);
  if (currentUser) {
    return <Redirect to="/dashboard" />;
  }

  return (
    <div>
      <Container className="p-3">
        <h2 className="fw-bold">Login</h2>
        <Form className="col-lg-4">
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label className="fw-bold">Email address:</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter email"
              name="userEmail"
              value={email}
              id="userEmail"
              onChange={(event) => onChangeHandler(event)}
            />
            <Form.Text className="text-muted">
              We'll never share your email with anyone else.
            </Form.Text>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label className="fw-bold">Password:</Form.Label>
            <Form.Control
              type="password"
              placeholder="Password"
              name="userPassword"
              value={password}
              id="userPassword"
              onChange={(event) => onChangeHandler(event)}
            />
          </Form.Group>
          <Button
            disabled={loader || success ? true : false}
            className="text-white"
            variant="primary"
            onClick={(event) => {
              signInWithEmailAndPasswordHandler(event, email, password);
            }}
          >
            {loader ? (
              <Spinner animation="border" variant="light" size="sm" />
            ) : (
              ""
            )}
            Sign In
          </Button>
          {success ? (
            <div>
              <Alert className="my-3 fw-bold rounded-pill" variant="success">
                <Spinner animation="border" variant="success" size="sm" />{" "}
                Signed in Successfully
              </Alert>
            </div>
          ) : (
            ""
          )}
          {error ? (
            <div>
              <Alert className="my-3 fw-bold rounded-pill" variant="danger">
                {errormessage}
              </Alert>
            </div>
          ) : (
            ""
          )}{" "}
          <p className="text-center my-3">
            Don't have an account? <Link to="/signUp">Sign up here</Link> <br />{" "}
            <Link to="/passwordReset">Forgot Password?</Link>
          </p>
        </Form>
      </Container>
    </div>
  );
};

export default Signin;
