import React from "react";
import S1 from "../assets/Sliders/S1.jpg";
import S2 from "../assets/Sliders/S2.jpg";
import S3 from "../assets/Sliders/S3.jpg";
import S4 from "../assets/Sliders/S4.jpg";
import { Carousel } from "react-bootstrap";

const Home = () => {
  return (
    <div>
      <Carousel variant="dark">
        <Carousel.Item>
          <img alt="First slide" className="d-block w-100" src={S1} />
        </Carousel.Item>
        <Carousel.Item>
          <img alt="Second slide" className="d-block w-100" src={S2} />
        </Carousel.Item>
        <Carousel.Item>
          <img alt="Third slide" className="d-block w-100" src={S3} />
        </Carousel.Item>
        <Carousel.Item>
          <img alt="Fourth slide" className="d-block w-100" src={S4} />
        </Carousel.Item>
      </Carousel>
    </div>
  );
};

export default Home;
