import React, { useContext } from "react";
import { Navbar, Nav, Container, Button, NavDropdown } from "react-bootstrap";
import logo from "../assets/DGE.png";
import "./header.css";
import { Link } from "react-router-dom";
import { AuthContext } from "../contexts/AuthContext";
import { auth } from "../config/firebase";

const Header = () => {
  const { currentUser } = useContext(AuthContext);

  return (
    <div>
      <Navbar
        className="shadow py-1"
        collapseOnSelect
        expand="lg"
        bg="light"
        variant="light"
      >
        <Container>
          <Navbar.Brand>
            <Link style={{ textDecoration: "none", color: "#4C4E52" }} to="/">
              <span
                className="bg-dark text-white rounded-pill px-3"
                id="nav-brand"
              >
                DGE
              </span>
            </Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse
            className="justify-content-end"
            id="responsive-navbar-nav"
          >
            <Nav>
              <Nav.Link component={Link} className="zoom fw-bold dark">
                <Link
                  className="hover-underline-animation"
                  style={{ textDecoration: "none", color: "#4C4E52" }}
                  to="/"
                >
                  Home
                </Link>
              </Nav.Link>
              <Nav.Link className="zoom fw-bold">
                <Link
                  className="hover-underline-animation"
                  style={{ textDecoration: "none", color: "#4C4E52" }}
                  to="/contact-us"
                >
                  Contact Us
                </Link>
              </Nav.Link>
              <Nav.Link className="zoom fw-bold">
                <Link
                  className="hover-underline-animation"
                  style={{ textDecoration: "none", color: "#4C4E52" }}
                  to="/about"
                >
                  About
                </Link>
              </Nav.Link>
              {currentUser ? (
                <div>
                  <NavDropdown
                    className="fw-bold hover-underline-animation"
                    title="Profile"
                    id="basic-nav-dropdown"
                  >
                    <NavDropdown.Item>
                      <Link
                        style={{ textDecoration: "none", color: "#4C4E52" }}
                        to="/dashboard"
                        className="text-dark"
                      >
                        Dashboard
                      </Link>
                    </NavDropdown.Item>
                    <NavDropdown.Item>
                      <Link
                        style={{ textDecoration: "none", color: "#4C4E52" }}
                        onClick={() => {
                          auth.signOut();
                        }}
                        className="text-dark"
                      >
                        Sign Out
                      </Link>
                    </NavDropdown.Item>
                  </NavDropdown>
                </div>
              ) : (
                <div>
                  <Link
                    style={{ textDecoration: "none", color: "#4C4E52" }}
                    to="/signIn"
                    className="text-white"
                  >
                    <Button className="text-white px-4" variant="primary">
                      <span className="text-white">Login</span>
                    </Button>
                  </Link>
                </div>
              )}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
};

export default Header;
